<template>
  <div id="app">
    <router-view/>
    <router-view name="payInfo"></router-view>
    <router-view name="share"></router-view>
    <router-view name='poolinfo'></router-view>
    <router-view name="dream"></router-view>
    <router-view name="luckyinfo"></router-view>
    <router-view name="user"></router-view>
    <router-view name="clock"></router-view>
    <router-view name="phone"></router-view>
    <router-view name="auth"></router-view>
    <router-view name="help"></router-view>
    <router-view name='fill'></router-view>
    <router-view name="list"></router-view>
    <router-view name="friend"></router-view>
    <router-view name="payRecord"></router-view>
    <router-view name="cach"></router-view>
    <router-view name="question"></router-view>
    <router-view name="notice"></router-view>
    <router-view name="history"></router-view>
    <router-view name="historyinfo"></router-view>
    <router-view name="tool"></router-view>
    <router-view name="tradeinfo"></router-view>
    <router-view name="agree"></router-view>
    <router-view name="rphome"></router-view>
    <router-view name="issue"></router-view>
    <router-view name="rpshare"></router-view>
    <router-view name="getpack"></router-view>
    <router-view name="open"></router-view>
    <router-view name="rprecord"></router-view>
    <router-view name="rprules"></router-view>
    <tab-bar v-if="istab"></tab-bar>
  </div>
</template>

<script>
import Tabbar from '@/components/tabbar/Tabbar'
export default {
  name:'App',
  data () {
    return {
      istab:true,
      state:'home',//梦想互助
    }
  },
  created(){
    // alert(1)
    if(!ExistStorage("userInfo")){
      window.location.href = 'http://tinydream.ivkcld.cn/TInydreamWeb/dream'
    }
    // 跳转
    if(GetStorage('state') != null){
      var type = GetStorage('state');
      RemoveStorage('state');
    }
    if(type == 'home'){
      this.$router.replace('/')
    }else if(type == 'clock'){
      this.$router.replace('/clock')
    }

    // 分享
    this.$store.commit('uid',JSON.parse(GetStorage('userInfo')).openid)
    var share = GetStorage('share');
    RemoveStorage('share')
    console.log(share)
    if(share != null){
      if(JSON.parse(share).hasOwnProperty('type')){
        if(JSON.parse(share).type == 'guide'){
          this.$router.push('/question')//分享--指引
        }else if(JSON.parse(share).type == 'clock'){
          if(JSON.parse(share).hasOwnProperty('url') && JSON.parse(share).url=="friend"){
            SaveStorage('friend',JSON.stringify({opid:JSON.parse(share).opid,type:JSON.parse(share).ctype}))
            this.$router.push('/friend');
          }else{
            this.$router.push('/clock')
          }
        }else if(JSON.parse(share).type == 'redpack'){
          if(JSON.parse(share).hasOwnProperty('rid')){
            this.$router.push('/grpack/'+JSON.parse(share).rid);
          }
        }else if(JSON.parse(share).hasOwnProperty('url')){
          if(JSON.parse(share).url == 'friend'){
            SaveStorage('share',JSON.stringify({opid:JSON.parse(share).opid,type:JSON.parse(share).type}))
            this.$router.push('/friend')
          }
        }
      }
    }else if(GetStorage('rid') != null){
      this.$router.push('/grpack/'+GetStorage('rid'));
    }else if(GetStorage('rphome') == 'redpack'){
      this.$router.push('/rphome')
    }

    //end
    var type = GetStorage('redpack');
    if(type == 'get'){
      var rid = JSON.parse(GetStorage('orp')).rid;
      RemoveStorage('type');
      RemoveStorage('orp');
      this.$router.push('/open/'+rid)
    }

    if(this.$route.path != '/' && this.$route.path != '/clock' && this.$route.path != '/user'){
      this.istab = false;
    }else{
      this.istab = true;
    }
  },
  methods:{
    
  },
  components:{
    TabBar:Tabbar
  },
  mounted(){
    this.$store.commit('uid',JSON.parse(GetStorage('userInfo')).openid)
  },
  watch:{
    $route(to,from){
      if (/iPhone|mac|iPod|iPad/i.test(navigator.userAgent)) {
        location.href = 'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/#' + to.path
      }
      this.$store.commit('uid',JSON.parse(GetStorage('userInfo')).openid)
      console.log(to.path,to)
      if(to.path != '/' && to.path != '/clock' && to.path != '/user'){
        this.istab = false;
      }else{
        this.istab = true;
      }
    }
  }
}
</script>


