<template>
    <div class="getpack">
        <div class="bg">
           <div class="main">
               <div class="icon">
                   <img :src="redpack != '' ? redpack.sender.headicon : ''" alt="">
               </div>
               <p class="nickname">{{redpack == '' ? '' : redpack.sender.nickname}}的梦想红包</p>
               <p class="msg">{{redpack == '' ? '' : redpack.redpack.content}}</p>
               <button class="btn" @click="openpack"></button>
           </div>
        </div>
    </div>
</template>

<script>
export default {
    name:'getpack',
    data () {
        return {
            rid:'',//红包 id
            redpack:'',//红包信息
        }
    },
    created(){
        if(Options.GetUserInfo() == null){
            SaveStorage('rid',this.$route.params.rid);
            window.location.href = 'http://tinydream.ivkcld.cn/TinydreamWeb/dream/index.html?time='+new Date().getTime()
        }else{
            if(GetStorage('rid') != null){
                var rid = GetStorage('rid');
            }else{
                var rid = this.$route.params.rid
            }
            RemoveStorage('rid')
            this.rid = rid;
            this.Register(this)
        }
    },
    methods:{
        Register(app){
            TD_Request('us','enter',{uid:Options.GetUserInfo().openid,nickname:Options.GetUserInfo().nickname,headicon:Options.GetUserInfo().headimgurl},function(code,data){
                app.grpack(app,app.rid);
            },function(code,data){
                console.log(data);
            })  
        },
        grpack(app,rid){
            TD_Request('rp','grp',{rid:rid},function(code,data){
                WebApp.JSAPI.InitShare({
                    title:data.sender.nickname+"给你发了一个梦想红包,领取可夺大额梦想金！",
                    desc:"小梦想互助——让每个小梦想 都有机会实现",
                    link:'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/html/share.html?type=redpack&state=yes&rid='+data.redpack.rid+'&time='+new Date().getTime(),
                    imgUrl:"https://tdream.antit.top/image/Red_Open.jpg"
                });
                console.log(data)
                if(data.redpack.content == ''){
                    data.redpack.content = '暂无祝福语'
                }
                app.redpack = data;
            },function(code,data){
                console.log(data)
            })
        },
        openpack(){
            this.$router.push('/open/'+this.rid);
        }
    }
}
</script>

<style lang="less" scoped>
    .getpack{
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: url(https://tdream.antit.top/image/Red_Background.jpg) no-repeat;
        background-size: cover;
        position: relative;
        .bg{
            height: 7.47rem;
            width: 100%;
            background: url(https://tdream.antit.top/image/Red_Open.jpg) no-repeat;
            background-size: 7.5rem 7.47rem;
            position: relative;
            top: 0.2rem;
            .main{
                width: 100%;
                padding-top: 2.4rem;
                .icon{
                    width: 0.88rem;
                    height: 0.88rem;
                    border-radius: 0.1rem;
                    margin: 0 auto 0.1rem;
                    img{
                        width: 0.88rem;
                        height: auto;
                    }
                }
                .nickname{
                    font-size: 0.3rem;
                    color: #ffc057;
                    margin-bottom: 0.1rem;
                    text-align: center;
                }
                .msg{
                    font-size: 0.36rem;
                    margin-bottom: 0.44rem;
                    color: #ffc057;
                    text-align: center;
                }
                button{
                    width: 1.68rem;
                    height: 1.68rem;
                    opacity: 0;
                    position: relative;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
        }
    }
</style>
