<template>
    <div class="record">
        <div class="main">
            <div class="topbar">
                <van-col span="12" :class="{active:active == 0 ? 'active' : ''}">
                    <div class="get" @click="toggle(0)">收<span class="underline">到的梦想红</span>包</div>
                </van-col>
                <van-col span="12" :class="{active:active == 1 ? 'active' : ''}">
                    <div class="give" @click="toggle(1)">发<span class="underline">出的梦想红</span>包</div>
                </van-col>
            </div>
            <div class="headicon">
                <img :src="userInfo.headimgurl" alt="">
            </div>
            <p class="nickname">{{userInfo.nickname + (active == 0 ? '收到' : '发出')}}的梦想红包共</p>
            <p class="num"><span style="font-size:1.2rem">{{countPack}}</span>个</p>
            <p class="tip">{{active == 0 ? '收到编号' : '发出红包金额'}}<span style="color:#f25542">{{active == 0 ? countPack : totalBill}}</span>{{active == 0 ? '个' : '元'}}</p>
        </div>
        <van-list 
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
        >
            <div class="list" v-for="(list,index) in getList" :key="index" @click="look(list.rid)">
                <van-col span="12">
                    <p class="packtype">{{active == 0 ? list.nickname : userInfo.nickname}}</p>
                    <p class="time">{{list.time}}</p>
                </van-col>
                <van-col span="12" style="text-align:right">
                    <p class="right">{{active == 0 ? list.pcount : list.rcount}}个</p>
                    <p class="ftip">{{active == 0 ? '' : list.status + list.gcount + '/' + list.rcount}}</p>
                </van-col>
            </div>
        </van-list>
    </div>
</template>

<script>
export default {
    'name':'rprecord',
    data(){
        return {
            active:0,
            userInfo:'',//用户信息
            loading:true,
            finished:false,
            getList:[],
            giveList:[],
            seek:0,
            countPack:'',
            totalBill:''
        }
    },
    created(){
        this.userInfo = Options.GetUserInfo();
        this.get(this,this.active)
    },
    methods:{
        onLoad(){
            this.seek+=10;
            this.get(this,this.active)
        },
        toggle(index){
            this.active = index;
            this.getList = []
            this.loading = true
            this.finished = false
            this.seek = 0
            this.get(this,index)
        },
        // 收到
        get(app,active){
            if(active == 0){
                var type = 'gurpr'
            }else {
                var type = 'gurps'
            }
            TD_Request('rp',type,{uid:app.$store.state.uid,seek:this.seek,count:10},function(code,data){
                console.log(data)
                app.loading = false;
                app.countPack = data.stats.countPack;
                app.totalBill = data.stats.totalBill / 100;
                if(data.packs.length  == 0){
                    app.finished = true;
                }
                $.each(data.packs,function(index,item){
                    if(active == 0){
                        var dt = new Date((item.gtime * 1000))
                    }else{
                        var dt = new Date((item.ptime * 1000))
                        var state = (item.pstate == 'FINISHED' && item.gcount < item.rcount) ? '已过期' : ''
                    }
                    var y = dt.getFullYear();
                    var m = (dt.getMonth()+1) < 10 ? '0'+(dt.getMonth()+1) : dt.getMonth()+1
                    var d = dt.getDate() < 10 ? '0' + dt.getDate() : dt.getDate();
                    var h = dt.getHours() < 10 ? '0' + dt.getHours() : dt.getHours();
                    var M = dt.getMinutes() < 10 ? '0'+dt.getMinutes() : dt.getMinutes()
                    var s = dt.getSeconds() < 10 ? '0' + dt.getSeconds() : dt.getSeconds();
                    var time = y + "-" + m + '-' + d + ' ' + h + ':' + M + ':' + s;
                    item.time = time;
                    item.status = state;
                    app.getList.push(item)
                })
                if(app.getList.length >= data.stats.countPack){
                    app.finished = true;
                }
            },function(code,data){
                console.log(data)
            })
        },
        look(rid){
            this.$router.push('/open/'+rid)
        }
    }
}
</script>

