<template>
    <div class="open">
        <div class="bg">
            <div class="headicon">
                <img :src="redpack == '' ? '' : redpack.sender.headicon" alt="">
            </div>
            <p class="nickname">{{redpack == '' ? '' : redpack.sender.nickname}}的梦想红包</p>
            <p class="content">{{redpack == '' ? '' : redpack.redpack.content}}</p>
            <p class="num">{{msg}}</p>
            <p class="tip">{{tip}}</p>
            <router-link class="fp" to="/issue" tag="p">我也要发红包</router-link>
        </div>
        <div class="center">
            <p class="title">{{count}}</p>
            <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
                style="padding-top:0.3rem"
                >
                <div class="main">
                    <div class="giveInfo" v-for="(reco,index) in reco" :key="index">
                        <van-col span="12">
                            <div class="icon">
                                <img :src="reco == '' ? '' : reco.headicon" alt="">
                            </div>
                            <div class="right">
                                <p class="nickname">{{reco == '' ? '' : reco.nickname}}</p>
                                <p class="time" style="color:#999">{{reco == '' ? '' : reco.time}}</p>
                            </div>
                        </van-col>
                        <van-col span="12" style="text-align:right">
                            <p class="lid">{{reco == '' ? '' : reco.lid}}</p>
                        </van-col>
                    </div>
                </div>
            </van-list>
        </div>
        <div class="bottom">
            <p class="msg">* 为了更好的保证您的利益，请您务必绑定手机、填写梦想</p>
            <button class="btn">下一步</button>
            <div class="qr">
                <img src="https://tdream.antit.top/LongPress2ShareQR.jpg" alt="">
            </div>
            <p style="color:#999;text-align:center;font-size:0.32rem" class="hide">长按识别，查看详情</p>
            <p style="color:#999;left:50%;transform: translateX(-50%);text-align: center;font-size:0.28rem;margin: 0.8rem 0 0;position: relative;;bottom: 0">小梦想互助——互助小额零钱，夺大额梦想金！</p>
        </div>
        <pop :show="ishow" :state="state"/>
    </div>
</template>

<script>
import pop from '@/components/dream/add/Add'
export default {
    name:'open',
    data () {
        return {
            redpack:'',
            msg:'待领取编号：1个',
            tip:'',
            ishow:false,
            state:'add',
            count:'',//红包领取状态
            seek:0,//seek
            loading:true,
            finished:false,
            reco:[],//领取人
        }
    },
    created(){
        this.grpack(this)
    },
    components:{
        pop,
    },
    methods:{
        onLoad(){
            this.seek+=10;
            this.getRP(this)
        },
        grpack(app){
            TD_Request('rp','grp',{rid:app.$route.params.rid},function(code,data){
                console.log(data)
                WebApp.JSAPI.InitShare({
                    title:data.sender.nickname+"给你发了一个梦想红包,领取可夺大额梦想金！",
                    desc:"小梦想互助——让每个小梦想 都有机会实现",
                    link:'http://tinydream.ivkcld.cn/TinydreamWeb/dream/dist/html/share.html?type=redpack&state=yes&rid='+data.redpack.rid+'&time='+new Date().getTime(),
                    imgUrl:"https://tdream.antit.top/image/Red_Open.jpg"
                });
                if(data.redpack.content == ''){
                    data.redpack.content = '暂无祝福语'
                }
                app.tip = '该编号自动参与小梦想互助平台'+data.redpack.pid+'期的幸运者抓阄，幸运者可以领取'+(data.redpack.ptbill / 100)+'元梦想互助金去启动梦想'
                app.redpack = data;
                app.led()
            },function(code,data){
                console.log(data)
            })
        },
        led(){
            var app = this;
            TD_Request('rp','orp',{uid:app.$store.state.uid,rid:app.$route.params.rid},function(code,data){
                console.log(data)
                $.each(data.nums,function(index,item){
                    app.msg = '已领取该编号：'+item.lid;
                })
                $('.btn').hide();
                $('.qr').show();
                $('.hide').show();
                app.getRP(app)
            },function(code,data){
                if(code == 72){
                    app.msg = '已领取过编号：'+data.reco.lid;
                    $('.btn').hide();
                    $('.qr').show();
                    $('.hide').show();
                    app.getRP(app)
                } else if (code == 11){
                    $('.btn').html('绑定手机').click(function(){
                        // SaveStorage('redpack','get');
                        app.finished = true;
                        SaveStorage('orp',app.$route.params.rid)
                        app.$router.push('/phone')
                    })
                }else if(code == 71){
                    app.finished = true;
                    $('.btn').html('添加梦想').click(function(){
                        app.ishow = true;
                    })
                }else if(code == 18){
                    app.tip = '您今日购买次数已达上限'
                    app.msg = ''
                    $('.btn').hide();
                    $('.qr').show();
                    $('.hide').show();
                    app.getRP(app)
                    $('.btn').html('返回首页').click(function(){
                        app.$router.push('/')
                    })
                }else if(code == 5){
                    app.tip = '红包已过期'
                    app.msg = ''
                    $('.btn').hide();
                    $('.qr').show();
                    $('.hide').show();
                    app.getRP(app)
                    $('.btn').html('返回首页').click(function(){
                        app.$router.push('/')
                    })
                }else if(code == 74){
                    app.tip = '您来晚了，红包已经被领完'
                    app.msg = ''
                    $('.btn').hide();
                    $('.qr').show();
                    $('.hide').show();
                    app.getRP(app)
                    $('.btn').html('返回首页').click(function(){
                        app.$router.push('/')
                    })
                }
            })
        },
        getRP(app){
            TD_Request('rp','grpr',{rid:app.$route.params.rid,seek:app.seek,count:10},function(code,data){
                console.log(data)
                app.loading = false;
                if(data.reco.length == 0){
                    app.finished = true;
                }
                $.each(data.reco,function(index,item){
                    var dt = new Date((item.gtime * 1000));
                    var h = dt.getHours() < 10 ? '0'+dt.getHours() : dt.getHours();
                    var m = dt.getMinutes() < 10 ? '0'+dt.getMinutes() : dt.getMinutes();
                    item.time = h+':'+m;
                    app.reco.push(item)
                })
                app.count = '共'+data.redpack.rcount+'个，已被领取'+data.redpack.gcount+'个'
                if(app.reco.length == data.redpack.rcount){
                    app.finished = true;
                }
            },function(code,data){
                console.log(data)
            })
        }
    }
}
</script>

